import React from "react";

const TableHeader = () => {
  return (
    <thead>
      <tr>
        <th>Name</th>
        <th>Job</th>
        <th>Hapus Data</th>
      </tr>
    </thead>
  );
};

const TableHeaderBarang = () => {
  return (
    <thead>
      <tr>
        <th>Nama Barang</th>
        <th>Harga Barang</th>
        <th>Hapus Barang</th>
      </tr>
    </thead>
  );
};

const TableBodyBarang = (props) => {
  const rows = props.barangData.map((row, index) => {
    return (
      <tr key={index}>
        <td>{row.namaBarang}</td>
        <td>{row.hargaBarang}</td>
        <td>
          <button onClick={() => props.removeBarang(index)}>Delete</button>
        </td>
      </tr>
    );
  });

  return <tbody>{rows}</tbody>;
}

const TableBody = (props) => {
  const rows = props.characterData.map((row, index) => {
    return (
      <tr key={index}>
        <td>{row.name}</td>
        <td>{row.job}</td>
        <td>
          <button onClick={() => props.removeCharacter(index)}>Delete</button>
        </td>
      </tr>
    );
  });

  return <tbody>{rows}</tbody>;
};

const Table = (props) => {
  const { characterData, removeCharacter, barangData, removeBarang} = props;
  return (
    <table>
      <TableHeader />
      <TableBody
        characterData={characterData}
        removeCharacter={removeCharacter}
      />
      <br></br>
      <br></br>
      <TableHeaderBarang />
      <TableBodyBarang barangData={barangData} removeBarang={removeBarang} />
    </table>
  );
};


export default Table;
